import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button } from '@mui/material';

export default function BasicTable() {
  const history = useHistory();
  const [appointments, setAppointments] = useState({ upcoming: [], past: [] });
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetch('http://localhost:8080/getAppointment') // Ensure the URL is correct
        .then(response => {
          if (!response.ok) {
            throw new Error('Network response was not ok');
          }
          return response.json();
        })
        .then(data => {
          setAppointments({
            upcoming: data.upcoming.map(app => ({
              ...app,
              applicationNo: app.appointment_id,  // Mapping appointment_id to applicationNo
            })),
            past: data.past.map(app => ({
              ...app,
              applicationNo: app.appointment_id,
            }))
          });
          setIsLoading(false);
        })
        .catch(error => {
          console.error('Error fetching appointments:', error);
          setError(error.message);
          setIsLoading(false);
        });
  }, []);

  const handleCancel = (applicationNo) => {
    const updatedUpcoming = appointments.upcoming.filter(app => app.applicationNo !== applicationNo);
    setAppointments(prev => ({ ...prev, upcoming: updatedUpcoming }));
    alert("Appointment canceled successfully!");
  };

  const handleReschedule = (appointment) => {
    history.push({
      pathname: '/make-appointment',
      state: { appointment } // Pass full appointment data
    });
  };

  const gotoPrescription = (applicationNo) => {
    history.push(`/prescription/${applicationNo}`);
  };

  if (isLoading) return <p>Loading...</p>;
  if (error) return <p>Error: {error}</p>;

  return (
      <div className="Table">
        <h3>Upcoming Appointments</h3>
        <TableContainer component={Paper} style={{ boxShadow: "0px 13px 20px 0px #80808029", borderRadius: "20px", marginBottom: "20px" }}>
          <Table sx={{ minWidth: 1100 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Application No.</TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Time</TableCell>
                <TableCell>Doctor</TableCell>
                <TableCell>Description</TableCell>
                <TableCell>Specialty</TableCell>
                <TableCell>Test</TableCell>
                <TableCell>Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {appointments.upcoming.map((appointment, index) => (
                  <TableRow key={index}>
                    <TableCell>{appointment.applicationNo}</TableCell>
                    <TableCell>{appointment.date}</TableCell>
                    <TableCell>{appointment.time}</TableCell>
                    <TableCell>{appointment.doctor}</TableCell>
                    <TableCell>{appointment.description}</TableCell>
                    <TableCell>{appointment.specialty}</TableCell>
                    <TableCell>{appointment.test}</TableCell>
                    <TableCell>
                      <Button color="success" variant="contained" onClick={() => handleReschedule(appointment)}>Reschedule</Button>
                      <Button color="error" variant="contained" onClick={() => handleCancel(appointment.applicationNo)}>Cancel</Button>
                    </TableCell>
                  </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>

        <h3>Past Appointments</h3>
        <TableContainer component={Paper} style={{ boxShadow: "0px 13px 20px 0px #80808029", borderRadius: "20px" }}>
          <Table sx={{ minWidth: 1100 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Application No.</TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Time</TableCell>
                <TableCell>Doctor</TableCell>
                <TableCell>Description</TableCell>
                <TableCell>Specialty</TableCell>
                <TableCell>Test</TableCell>
                <TableCell>Prescription</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {appointments.past.map((appointment, index) => (
                  <TableRow key={index}>
                    <TableCell>{appointment.applicationNo}</TableCell>
                    <TableCell>{appointment.date}</TableCell>
                    <TableCell>{appointment.time}</TableCell>
                    <TableCell>{appointment.doctor}</TableCell>
                    <TableCell>{appointment.description}</TableCell>
                    <TableCell>{appointment.specialty}</TableCell>
                    <TableCell>{appointment.test}</TableCell>
                    <TableCell>
                      <Button onClick={() => gotoPrescription(appointment.applicationNo)}>Show</Button>
                    </TableCell>
                  </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
  );
}
