import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useAuth } from './AuthContext'; // Import useAuth from your context
import { LoginData } from '../../../Data/SignUpData';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';

const Login = () => {
    const history = useHistory();
    const { login } = useAuth(); // Use login function from AuthContext
    const [formData, setFormData] = useState({
        email: '',
        password: '',
        role: ''
    });
    const [errors, setErrors] = useState({});
    const [showPassword, setShowPassword] = useState(false);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({ ...prevState, [name]: value }));
    };

    const handleTogglePasswordVisibility = () => {
        setShowPassword(!showPassword);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!formData.email || !formData.password || !formData.role) {
            alert("All fields are required!");
            return;
        }
        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
            alert("Invalid email format!");
            return;
        }

        const response = await fetch('/login', {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(formData)
        });
        const data = await response.json();

        if (response.ok && data.success) { // Ensure that both the HTTP response and your success flag are checked
            localStorage.setItem('token', data.token);
            localStorage.setItem('patient_id', data.patient_id);
            login(); // Update authentication state
            history.push(`/patient/${data.patient_id}`);
            alert('Login successful!');
        } else {
            console.error('Login failed:', data.message);
            alert(data.message || "Login failed");
            setErrors({ form: data.message || "Failed to login, please try again." });
        }
    };



    return (
        <form onSubmit={handleSubmit}>
            {LoginData.map((field, index) => (
                <div className="form-group" key={index}>
                    <label htmlFor={field.name}>{field.label}</label>
                    <div style={{ position: 'relative' }}>
                        {field.type === "select" ? (
                            <select id={field.name} name={field.name} value={formData[field.name]} onChange={handleChange} className="text-input">
                                {field.options.map(option => <option key={option.value} value={option.value}>{option.label}</option>)}
                            </select>
                        ) : (
                            <input type={field.name === "password" && !showPassword ? "password" : "text"} id={field.name} name={field.name} placeholder={field.placeholder} value={formData[field.name]} onChange={handleChange} className="text-input" />
                        )}
                        {field.name === "password" && (
                            <span onClick={handleTogglePasswordVisibility} style={{ position: 'absolute', right: '10px', top: '8px', cursor: 'pointer' }}>
                                {showPassword ? <VisibilityOffIcon /> : <RemoveRedEyeIcon />}
                            </span>
                        )}
                    </div>
                    {errors[field.name] && <div className="error">{errors[field.name]}</div>}
                </div>
            ))}
            {errors.form && <div className="error">{errors.form}</div>}
            <button type="submit" className="submit-btn">Sign In</button>
        </form>
    );
};

export default Login;
