// PrescriptionDetails.jsx
import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Paper, Typography, Table, TableBody, TableCell, TableHead, TableRow } from '@mui/material';

function PrescriptionDetails() {
  const { applicationId } = useParams();
  const [prescription, setPrescription] = useState(null);

  // Mock function to simulate fetching data
  useEffect(() => {
    const fetchPrescription = async () => {
      const fetchedPrescription = {
        applicationId: applicationId,
        patientName: 'Priya Patel',
        doctor: 'Dr. Smith',
        date: '2024-04-10',
        diagnosis: 'Hypertension',
        description: 'Increased blood pressure readings over the last three months.',
        medicines: [
          { name: 'Medicine 1', qty: '30', unit: 'Capsules', dosage: '1', frequency: 'Twice a day' },
          { name: 'Medicine 2', qty: '1', unit: 'Tube', dosage: '1', frequency: 'Twice a day' },
        ]
      };
      setPrescription(fetchedPrescription);
    };
    fetchPrescription();
  }, [applicationId]);

  if (!prescription) return <div>Loading...</div>;

  return (
    <Paper style={{ padding: "20px", margin: "20px" }}>
      <Typography variant="h4" style={{ marginBottom: "20px" }}>Prescription Details</Typography>
      <Typography><strong>Patient Name:</strong> {prescription.patientName}</Typography>
      <Typography><strong>Doctor:</strong> {prescription.doctor}</Typography>
      <Typography><strong>Date:</strong> {prescription.date}</Typography>
      <Typography><strong>Diagnosis:</strong> {prescription.diagnosis}</Typography>
      <Typography><strong>Description:</strong> {prescription.description}</Typography>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Medicine</TableCell>
            <TableCell>Quantity</TableCell>
            <TableCell>Unit</TableCell>
            <TableCell>Dosage</TableCell>
            <TableCell>Frequency</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {prescription.medicines.map((med, index) => (
            <TableRow key={index}>
              <TableCell>{med.name}</TableCell>
              <TableCell>{med.qty}</TableCell>
              <TableCell>{med.unit}</TableCell>
              <TableCell>{med.dosage}</TableCell>
              <TableCell>{med.frequency}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Paper>
  );
}

export default PrescriptionDetails;
