import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Button from "@mui/material/Button";

function createData(name, gender, birth, mobile, email, medicalHistory) {
  return { name, gender, birth, mobile, email, medicalHistory };
}

const rows = [
  createData("John Doe", "Male", "15 April 1985", "1234567890", "johndoe@example.com", "None"),
  createData("Jane Smith", "Female", "23 June 1990", "0987654321", "janesmith@example.com", "Allergy"),
];

export default function BasicTable() {
  return (
    <div className="Table">
      <h3>User Registration Requests</h3>
      <TableContainer
        component={Paper}
        className="table-container"
        style={{ boxShadow: "0px 13px 20px 0px #80808029", borderRadius: "20px" }}
      >
        <Table sx={{ minWidth: 1200 }} aria-label="simple table" stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Gender</TableCell>
              <TableCell>Birth</TableCell>
              <TableCell>Mobile</TableCell>
              <TableCell>Email</TableCell>
              <TableCell>Medical History</TableCell>
              <TableCell>Operation</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow key={index}>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.gender}</TableCell>
                <TableCell>{row.birth}</TableCell>
                <TableCell>{row.mobile}</TableCell>
                <TableCell>{row.email}</TableCell>
                <TableCell>{row.medicalHistory}</TableCell>
                <TableCell>
                  <Button color="success" variant="contained" style={{ marginRight: 8 }}>Approve</Button>
                  <Button color="error" variant="contained">Reject</Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
