import React, { useState, useEffect } from 'react';
import { Link as ScrollLink } from "react-scroll";
import { useAuth } from './Login/AuthContext';
import CloseIcon from "@mui/icons-material/Close";
import MenuIcon from "@mui/icons-material/Menu";
import Modal from 'react-modal';
import Login from './Login/Login';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';

const Header = () => {
    const { isAuthenticated, logout } = useAuth();
    const [sidebar, setSidebar] = useState(false);
    const [modalIsOpen, setModalIsOpen] = useState(false);

    useEffect(() => {
        if (!isAuthenticated && modalIsOpen) {
            setModalIsOpen(false); // Automatically close modal if not authenticated
        }
    }, [isAuthenticated]);

    const toggleSignInOut = () => {
        if (isAuthenticated) {
            logout();
        } else {
            setModalIsOpen(true);
        }
    };

    return (
        <header className='header'>
            <div className="title">
                <h1>E-CLINIC</h1>
            </div>
            <div className="nav flex">
                <ul className={sidebar ? "nav-links-sidebar" : "nav-links"} onClick={() => setSidebar(false)}>
                    <li><ScrollLink to="home" smooth={true} duration={500}>Home</ScrollLink></li>
                    <li><ScrollLink to="service" smooth={true} duration={500}>Service</ScrollLink></li>
                    <li><ScrollLink to="contact" smooth={true} duration={500}>Contact</ScrollLink></li>
                    <button onClick={toggleSignInOut}>
                        {isAuthenticated ? 'Sign Out' : 'Sign In'}
                    </button>
                </ul>
                <div className='navbar-items-icon' onClick={() => setSidebar(!sidebar)}>
                    {sidebar ? <CloseIcon /> : <MenuIcon />}
                </div>
            </div>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={() => setModalIsOpen(false)}
                style={{ overlay: { background: "rgba(0, 0, 0, 0.5)" }, content: { margin: "auto", width: "450px", height: "600px" } }}
            >
                <HighlightOffIcon onClick={() => setModalIsOpen(false)} style={{ cursor: 'pointer', position: 'absolute', right: '10px', top: '10px' }} />
                <Login />
            </Modal>
        </header>
    );
};

export default Header;
